<template>
	<div class="page-list">
		<el-tabs type="border-card">
			<el-tab-pane label="">
				<el-table :data="items">
					<el-table-column prop="_id" label="ID" width="230"> </el-table-column>
					<el-table-column prop="name" label=""> </el-table-column>
					<el-table-column prop="desc" label=""> </el-table-column>
					<el-table-column fixed="right" label="" width="180">
						<template slot-scope="scope">
							<el-button
								type="text"
								size="small"
								@click="$router.push(`/categories/edit/${scope.row._id}`)"
								></el-button
							>
							<el-button type="text" size="small" @click="remove(scope.row)"
								></el-button
							>
						</template>
					</el-table-column>
				</el-table>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				items: [],
			};
		},
		methods: {
			async fetch() {
				const res = await this.$http.get('rest/categories');
				this.items = res.data;
			},
			async remove(row) {
				this.$confirm(`? '${row.name}'`, '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning',
				})
					.then(async () => {
						const res = await this.$http.delete(`rest/categories/${row._id}`);
						this.$message({
							type: 'success',
							message: '!',
						});
						this.fetch();
					})
					.catch(() => {});
			},
		},
		created() {
			this.fetch();
		},
	};
</script>

<style scoped>
	.page-edit {
		padding: 0 1rem 1rem 1rem;
	}
</style>
